import React from 'react'
import {useDispatch} from 'react-redux'
import cl from './CartMenuItem.module.scss'
import cartTrash from '../../Assets/Image/cart-trash.svg'
import { deleteProductFromCart } from '../../redux/cart/reducer'
const CartMenuItem = ({products}) => {
    const dispatch = useDispatch()
    const handleClick = (id) => {
        dispatch(deleteProductFromCart(id))
    }


  return (
    <div className={cl.cart}>
        <div className={cl.cart__left}>
            
            <img src={products.image} alt="img" />

            <div className={cl.cart__left__info}>
                <p>{products.title}</p>

                <span>{products.price} </span>
            </div>
        </div>
        <div className={cl.cart__right}>
            <img src={cartTrash} alt="trash" onClick={() => handleClick(products.id)}/>
        </div>
    </div>
  )
}

export default CartMenuItem