import React, { useCallback } from "react";
import CartMenuItem from "../CartMenuItem/CartMenuItem";
import SimpleBar from "simplebar-react";
import "simplebar/dist/simplebar.min.css";
import cl from "./ModalOrder.module.scss";
import ModalOrderForm from "../ModalOrderFom/ModalOrderForm";

const ModalOrder = ({ active, setActive, product, setActiveMenu }) => {
  const handleClick = useCallback(() => {
    setActive(false);
    setActiveMenu(true)
  }, [setActive, setActiveMenu]);
  return (
    <div className={active ? "modal active" : "modal"} onClick={handleClick}>
      <div
        className={active ? "modal__content active" : "modal__content"}
        onClick={(e) => e.stopPropagation()}
      >
        <div className={cl.modal__title}> </div>

        <div className={cl.modal__content}>
          <div className={cl.modal__info}>
            <p className={cl.modal__info__title}>  :</p>
            <span className={cl.modal__info__product}>{product.length} </span>
          </div>

          <div className={cl.modal__info}>
            <p className={cl.modal__info__title}>  :</p>
            <span className={cl.modal__info__product}>
              {product.reduce((acc, el) => (acc += el.price), 0)} 
            </span>
          </div>

          <SimpleBar style={{ maxHeight: 280 }}>
            {product.map((product) => (
              <CartMenuItem products={product} key={product.id} />
            ))}
          </SimpleBar>
        </div>
        <ModalOrderForm />
        
      </div>
    </div>
  );
};

export default ModalOrder;
