import React, { useState } from "react";
import Nouislider from "nouislider-react";
import "nouislider/distribute/nouislider.css";
import "./UiSlider.scss";

const UiSlider = () => {
  const [valueInputStart, setValueInputStart] = useState(0);
  const [valueInputEnd, setValueInputEnd] = useState(0);
  const update = (values) => {
    setValueInputStart(values[0]);
    setValueInputEnd(values[1]);
  };
  return (
    <div>
      <label className="label__input-ui">
        <input type="number" value={valueInputStart} className= 'ui-input'/>
        <input type="number" value={valueInputEnd} className= 'ui-input'/>
      </label>
      <Nouislider
        range={{ min: 0, max: 25768 }}
        start={[0, 25768]}
        connect
        step={10}
        onUpdate={(values) => update(values)}
      />
    </div>
  );
};

export default UiSlider;
