import React from "react";
import { useForm } from "react-hook-form";
import InputModalOrderForm from "../UI/InputModalOrderForm/InputModalOrderForm";
import OrderButton from "../UI/OrderButton/OrderButton";
import "./ModalOrderForm.scss";
const ModalOrderForm = () => {
  const {
    register,
    formState: { errors, isValid },
    handleSubmit,
    reset
  } = useForm({
    mode: 'onBlur'
  });

  const onSubmit = (data) => {
    alert(JSON.stringify(data));
    reset()
  };
  return (
    <form className="form" onSubmit={handleSubmit(onSubmit)}>
        <InputModalOrderForm
          reg = {{...register("firstName", {
            required: '   ',
            maxLength: {
              value: 10,
              message: '  10 '
            }
          })}}
          type="text"
          placeholder=" "
        />
        {errors?.firstName && <p style={{margin: 0, color: 'red', fontSize: '14px'}}>{errors?.firstName?.message || 'Error'}</p>}
      <InputModalOrderForm 
        type="number" 
        placeholder=" "
        reg = {{...register("secName", {
          required: '   ',
          minLength: {
            value: 11,
            message: '  11 '
          }
        })}} 
      />
      {errors?.secName && <p style={{margin: 0, color: 'red', fontSize: '14px'}}>{errors?.secName?.message || 'Error'}</p>}
      <InputModalOrderForm 
        type="email" 
        placeholder="E-mail"
        reg = {{...register("thirdName", {
          required: '   ',
          maxLength: 35
        })}}  
      />
      {errors?.thirdName && <p style={{margin: 0, color: 'red', fontSize: '14px'}}>{errors?.thirdName?.message || 'Error'}</p>}
      {isValid ? <OrderButton type="submit"> </OrderButton> : <OrderButton disabled> </OrderButton>}
    </form>
  );
};

export default ModalOrderForm;
