import React from 'react'
import cl from './AddToCart.module.scss'
import addToCart from '../../../Assets/Image/addToCart.svg'
const AddToCartButton = ({children, product, onClick, ...props}) => {
  return (
    <button {...props} onClick = {onClick} className = {cl.btn} style = {{background:  `#444B58 url(${addToCart}) no-repeat center`}}>
        {children}
    </button>
  )
}

export default AddToCartButton