import React from 'react'
import { useCallback } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { deleteProduct } from '../../redux/product/reducer'
import ProductModalItem from '../ProductModalItem/ProductModalItem'
import './Modal.scss'
const Modal = ({active, setActive, product}) => {
  const dispatch = useDispatch()
  const products = useSelector((state) => state.product.currentProduct)

  const handleClick = useCallback(() => {
    setActive(false)
    setTimeout(() => {
      dispatch(deleteProduct(product))
    }, 500)
  }, [dispatch, product, setActive])

  return (
    <div className={active ? 'modal active' : 'modal'} onClick={handleClick}>
      <div className={active ? 'modal__content active' : 'modal__content'} onClick={(e) => e.stopPropagation()}>
        {products.map(product => (
          <ProductModalItem product={product} key ={product.id}/>
        ))}
      </div>
    </div>
  )
}

export default Modal