import React from "react";
import FakeCheckbox from "../FakeCheckbox/FakeCheckbox";
import ProductLIst from "../ProductList/ProductLIst";
import SubmitButton from "../UI/SubmitButton/SubmitButton";
import UiSlider from "../UI/UiSlider/UiSlider";
import "./Catalog.scss";

const Catalog = () => {
  return (
    <div className="catalog">
      <div className="container">
        <h3 className="catalog__title"></h3>

        <div className="catalog__content">
          <div className="catalog__left">
            <div className="catalog__left-title">  </div>
            <span className="catalog__left-section-title"> .</span>

            <UiSlider/>
            <span className="catalog__left-section-title"></span>

            <FakeCheckbox />

            <span className="catalog__left-section-title"></span>

            <table>
              <tbody>
                <tr>
                  <td>35</td>
                  <td>36</td>
                  <td>37</td>
                </tr>
                <tr>
                  <td>38</td>
                  <td>39</td>
                  <td>40</td>
                </tr>
                <tr>
                  <td>41</td>
                  <td>42</td>
                  <td>43</td>
                </tr>
              </tbody>
            </table>

            <SubmitButton></SubmitButton>

            <span className="delete__btn"></span>
          </div>

          <div className="catalog__right">
              <ProductLIst/>
              
          </div>
        </div>
      </div>
    </div>
  );
};

export default Catalog;
