import React from 'react'
import cl from './InspectButton.module.scss'
import inspect from '../../../Assets/Image/inspect.svg'
const InspectButton = ({children, ...props}) => {
  return (
    <button className={cl.btn} {...props} style = {{background:  `#444B58 url(${inspect}) no-repeat center`}}>
        {children}
    </button>
  )
}

export default InspectButton