import React, { useState } from "react";
import AddToCartButton from "../UI/AddToCartButton/AddToCartButton";
import InspectButton from "../UI/InspectButton/InspectButton";
import "./ProductItemButton.scss";
import { useDispatch, useSelector } from "react-redux";
import {
  setProductInCart,
} from "../../redux/cart/reducer";
import Modal from "../Modal/Modal";
import { setProduct } from "../../redux/product/reducer";
import { useCallback } from "react";

const ProductItemButtons = ({ product }) => {
  const dispatch = useDispatch();
  const items = useSelector((state) => state.cart.productsInCart);
  const isProductInCart = items.some((item) => item.id === product.id);
  const [visibleModal, setVisibleModal] = useState(false)

  const handleClick = useCallback((e) => {
    e.stopPropagation();
    dispatch(setProductInCart(product));
  }, [dispatch, product]);

  const setProductInModal = useCallback((e) => {
    setVisibleModal(true)
    dispatch(setProduct(product))
  }, [dispatch, product])

  return (
    <div >
      <div className="product__item-buttons">
        <InspectButton onClick = {setProductInModal}/> 
        {isProductInCart 
            ? null
            : <AddToCartButton product={product} onClick={handleClick} />
        }
        </div>
      <Modal active = {visibleModal} setActive={setVisibleModal} product={product}/>
    </div>
    
  );
};

export default ProductItemButtons;
