import React, { useState } from "react";
import SimpleBar from "simplebar-react";
import "simplebar/dist/simplebar.min.css";
import CartMenuItem from "../CartMenuItem/CartMenuItem";
import CartOrderButton from "../UI/CartOrderButton/CartOrderButton";
import { CSSTransition, TransitionGroup } from "react-transition-group";
import "./CartMenu.scss";
import ModalOrder from "../ModalOrder/ModalOrder";
import { useSelector } from "react-redux";
const CartMenu = ({ products }) => {
  const [visibleModal, setVisibleModal] = useState(false);
  const [visibleMenu, setVisibleMenu] = useState(true);
  const items = useSelector((state) => state.cart.productsInCart);
  const handleClick = (e) => {
    setVisibleModal(true);
    setVisibleMenu(false);
  };
  return (
    <div>
      <ModalOrder
        active={visibleModal}
        setActive={setVisibleModal}
        product={items}
        setActiveMenu = {setVisibleMenu}
      />
      {visibleMenu ? (
        <div className="cart__menu">
          <div className="cart__menu-top">
            {products.length >= 1 ? (
              <SimpleBar style={{ maxHeight: 350 }}>
                <TransitionGroup>
                  {products.map((product) => (
                    <CSSTransition
                      key={product.id}
                      timeout={500}
                      classNames="menu"
                    >
                      <CartMenuItem products={product} key={product.id} />
                    </CSSTransition>
                  ))}
                </TransitionGroup>
              </SimpleBar>
            ) : (
              <div className="empty-cart"> </div>
            )}
          </div>
          <div className="cart__menu-bottom">
            <div className="cart__menu-bottom-left">
              <p className="cart__menu-bottom-left-total">:</p>

              <span className="total">
                {products.length < 0
                  ? 0
                  : products.reduce((acc, el) => (acc += el.price), 0)}{" "}
                
              </span>
            </div>
            <div className="cart__menu-bottom-right">
              {products.length >= 1 ? (
                <CartOrderButton onClick={handleClick}>
                    
                </CartOrderButton>
              ) : null}
            </div>
          </div>
        </div>
      ) : null}
    </div>
  );
};

export default CartMenu;
