import React, { useCallback } from 'react'
import { useDispatch, useSelector} from 'react-redux';
import { setProductInCart } from '../../redux/cart/reducer';
import ProductModalImage from '../ProductModalImage/ProductModalImage';
import ModalOrderButton from '../UI/ModalOrderButton/ModalOrderButton';
import cl from './ProductModalItem.module.scss';

const ProductModalItem = ({product}) => {
    const items = useSelector((state) => state.cart.productsInCart);
    const isProductInCart = items.some((item) => item.id === product.id);
    const dispatch = useDispatch()
    const handleClick = useCallback(() => {
        dispatch(setProductInCart(product))
    }, [dispatch, product])

  return (
    <div className={cl.product}>
        <div className={cl.product__left}>
            <ProductModalImage image={product.image}/>
            <span></span>
            <p className={cl.product__descr}>
                {product.description}
            </p>
        </div>
        <div className={cl.product__right}>
            <div className={cl.product__title}>{product.title}</div>
            
            <span className={cl.product__genre__title}>:</span>

            <div className={cl.product__genre}>
                {product.genres.map(genre => (
                    <div key={genre}>
                        {genre}
                    </div>
                    ))}
            </div>

            <span className={cl.product__price}>{product.price} </span>

            <div className={cl.product__btn}>
            
              {!isProductInCart ? <ModalOrderButton onClick={handleClick}></ModalOrderButton> : <ModalOrderButton disabled>  !</ModalOrderButton>}
            </div>

            
        </div>
    </div>
  )
}

export default ProductModalItem